<template>
  <div>
    <img class="raffle" @click="raffle();$bvModal.show('bv-modal-example')" src="../assets/抽籤筒.png" />
    <h2>Click this</h2>
    <b-modal id="bv-modal-example" hide-footer>
      <template v-slot:modal-title>您抽到的卡是 ID: {{raffleOneCard.id + 1}}</template>
      <div class="d-block text-center">
        <b-card
          :img-src="require(`../assets/${raffleOneCard.imgSrc}`)"
          img-alt="Image"
          img-top
          tag="article"
          style="max-width: 100%;"
          class="mb-2"
        >
          <b-card-img />
          <b-card-title>{{raffleOneCard.name}}</b-card-title>
          <b-card-text>{{raffleOneCard.article}}</b-card-text>
        </b-card>
      </div>
      <b-button class="mt-3" block @click="$bvModal.hide('bv-modal-example')">Close Me</b-button>
      <b-button class="mt-3" block @click="goToTakePictures(raffleOneCard.id)">製作小卡</b-button>
    </b-modal>
  </div>
</template>
<style lang="scss" scoped>
.raffle {
  cursor: pointer;
}
</style>
<script>
// @ is an alias to /src
import { mapGetters /*mapActions*/ } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["raffleOneCard"])
  },
  methods: {
    //...mapActions(["updateItem_1"]),
    /*...mapActions([]) 是用在從 server 抓資料到 state 裡使用，
     *這個動作要用Action 初始化，但不需帶參數(paylod)寫*/
    async raffle() {
      await this.$store.dispatch("getRaffleOneCard");
    },

    goToTakePictures(mainId) {
      this.$router.push({
        path: "/takePictures",
        query: { inputEssentialOilCard: mainId }
      });
    }
  }
};
</script>
